#appbar{
width: 100%;
margin-left: -1px;
}
#rayan{
cursor: pointer;
font-family: Arial, Helvetica, sans-serif;
font-size: 25px;
font-weight: bold;
}
#btn{
    display: none;
}
#btn_nav_S{
display: block;
color: black;
}
#icon,#close{
font-size: 25px;
left: 81%;
top: 25%;
cursor: pointer;
position: absolute;
display: none;
}
#close{
    display: none;
}
#text{
    font-size: 70px;
    font-family: Arial, Helvetica, sans-serif;
    margin-left: 5%;
}
#ray{
    font-size: 72px;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: 500;
}
@media (max-width:500px){
    #icon{
        display: block;
    }
    #btn_nav_S{
        display: none;
    }
    #btn{
        position: absolute;
        left: 45%;
    }
}
